<template>
  <!-- search事件同时要绑定在van组件,监听移动设备的输入法键盘回车 -->
  <van-search
    v-model="value"
    shape="round"
    show-action
    @search="onSearch"
    placeholder="搜索文章"
  >
    <template #action>
      <span @click="onSearch">搜索</span>
    </template>
  </van-search>
</template>

<script>
export default {
  name: 'BaseSearchBar',
  data() {
    return {
      value: ''
    }
  },

  mounted() {},

  methods: {
    onSearch() {
      this.$bus.$emit('header-search', this.value)
      this.value = ''
    }
  }
}
</script>

<style lang="stylus"></style>