<template>
  <div class="blogm-header">
    <component
      :is="headType"
      :title="title"
      :icon="actualIcon"
      :button="actualButton"
      :btnHandler="actualBtnHandler"
    ></component>
  </div>
</template>

<script>
import NavBar from '@/components/mobile/base/BaseNavBar'
import SearchBar from '@/components/mobile/base/BaseSearchBar'
export default {
  name: 'VueBlogMHeader',
  components: { NavBar, SearchBar },
  data() {
    return {
      headType: 'NavBar',
      title: '标题',
      icon: '',
      button: '',
      btnHandler: ''
    }
  },
  //创建时重置Header 所有顶端配置以route meta headCfg为准
  created() {
    this.resetHeader(this.$route)
  },
  //监听来自其他地方的动态修改请求
  mounted() {
    this.$bus.$off('switch-user-header', this.switchUserHeader)
    this.$bus.$on('switch-user-header', this.switchUserHeader)
  },
  computed: {
    isLogin() {
      return this.$store.state.token
    },
    //位于用户中心
    isUser() {
      return this.$route.name === 'mUser' && this.isLogin
    },
    //根据状态计算属性实际决定右上角渲染
    actualIcon() {
      return this.isUser ? '' : this.icon
    },
    actualButton() {
      return this.isUser ? '' : this.button
    },
    actualBtnHandler() {
      return this.isUser ? '' : this.btnHandler
    }
  },
  watch: {
    /*
      如果跳转两者路径是一致的 不要重置header(用户界面切换的时候直接修改了button
      push之后 $route监听 又会重置一遍 导致切换按钮混乱
    */
    $route(to, from) {
      if (to.name === from.name) return false
      this.resetHeader(to)
      //从dialog跳转User的文字修改兜底
      if (to.name === 'mUser' && to.query.state === 'register') {
        this.button = '登录'
      }
    }
  },
  methods: {
    //TODO 如果用户已经登录了 要清除掉右上角按钮
    switchUserHeader() {
      //判断 是否显示"登录" 即右上角为注册切换
      let isViewLogin = !!(this.button === '注册')
      this.button = isViewLogin ? '登录' : '注册'
      this.$router.push({
        name: 'mUser',
        query: { state: isViewLogin ? 'register' : 'login' }
      })
    },
    resetHeader(route) {
      //如果动态修改请求时非watch传参 需要重新获取route
      //route = route ?? this.$route
      this.icon = ''
      this.button = ''
      this.btnHandler = ''
      Object.assign(this, route.meta.headCfg)
    }
  }
}
</script>

<style lang="stylus"></style>