<template>
  <van-dialog
    v-model="show"
    :title="title"
    show-cancel-button
    :before-close="beforeClose"
    confirm-button-text="提交"
    confirm-button-color="#1A73E8"
  >
    <BaseForm ref="form" :formType="type" />
    <p v-if="type === 'login'" class="blogm-dialog--route" @click="goRegister">
      还没有账号?点击跳转到注册
    </p>
  </van-dialog>
</template>

<script>
import MODAL_CONFIG from '@/configs/modal.config'
import BaseForm from '@/components/mobile/base/BaseForm.vue'
import { Toast } from 'vant'

export default {
  name: 'VueBlogBaseDialog',
  components: { BaseForm },
  data() {
    return {
      show: false,
      type: null
    }
  },

  mounted() {
    this.$bus.$on('show-dialog', this.showDialog)
  },
  beforeDestroy() {
    this.$bus.$off('show-dialog', this.showDialog)
  },
  computed: {
    title() {
      return MODAL_CONFIG?.[this.type]?.title || '默认对话框'
    }
  },
  methods: {
    showDialog(type) {
      this.type = type
      this.show = true
    },
    beforeClose(action, done) {
      /*
        所有关闭行为的事件接管
        调用 done() 后关闭弹窗，
        调用 done(false) 阻止弹窗
      */
      if (action === 'confirm') {
        this.submitForm(done)
        return
      }
      done()
    },
    async submitForm(done) {
      const refForm = this.$refs['form']
      const vanForm = refForm.$refs['vanForm']
      try {
        await vanForm.validate()
        await this.$http({
          type: this.type,
          data: refForm.form
        })
        //完成后展示评论框
        this.$bus.$emit('show-art-comment')
        done()
      } catch (err) {
        Toast({ message: '请检查您的输入内容', position: top })
        //重置
        refForm.form = {}
        vanForm.resetValidation()
        done(false)
      }
    },
    //注册链接
    goRegister() {
      //dialog组件有对右上角文字的兜底
      this.$router.push({ name: 'mUser', query: { state: 'register' } })
      this.show = false
    }
  }
}
</script>

<style lang="stylus" >
.blogm-dialog--route
  font-size 14px
  text-align right
  padding 5px 10px
  color theme-color-orange
</style>