<template>
  <van-nav-bar
    :title="title"
    :right-text="button"
    left-arrow
    @click-left="$router.back()"
    @click-right="onClickRight"
  >
    <template #right>
      <van-button type="default" :icon="icon" v-if="button">{{
        button
      }}</van-button>
    </template>
  </van-nav-bar>
</template>

<script>
export default {
  name: 'BaseNavBar',
  props: {
    title: {
      type: String
    },
    icon: {
      type: String
    },
    button: {
      type: String
    },
    btnHandler: {
      type: String
    }
  },
  data() {
    return {}
  },

  methods: {
    onClickRight() {
      this.$bus.$emit(this.btnHandler)
    }
  }
}
</script>

<style lang="stylus">
.van-nav-bar .van-button.van-button--default
  border none
</style>